<template>
  <!--手机号登录-->
  <div>
    <a-form-model ref="formLogin" :rules="rules" :model="formLogin" @submit="login" @submit.native.prevent>
      <a-form-model-item prop="phone">
        <a-input v-model="formLogin.phone" placeholder="请输入手机号" size="large">
          <a-icon slot="prefix" type="phone" style="color:rgba(0,0,0,.25)"/>
        </a-input>
      </a-form-model-item>
      <a-form-model-item prop="code">
        <a-input-search v-model.number="formLogin.code" placeholder="输入验证码" size="large" @search="sendCode">
          <a-icon slot="prefix" type="code" style="color:rgba(0,0,0,.25)"/>
          <a-button slot="enterButton">
            获取验证码
          </a-button>
        </a-input-search>
      </a-form-model-item>
      <div class="row checkbox">
        <a-form-model-item>
          <a-checkbox :checked="formLogin.checked" @change="rememberMe">
            记住登录状态
          </a-checkbox>
        </a-form-model-item>
      </div>
      <a-form-model-item>
        <a-button
            type="primary" block
            html-type="submit"
            size="large"
            :loading="loading"
        >
          登 录
        </a-button>
      </a-form-model-item>
    </a-form-model>
  </div>
</template>

<script>
  import request from "@/utils/request";
  import storage from "store";

  export default {
    name: 'LoginPhone',
    data() {
      let validatorPhone = (rule, value, callback) => {
        // 正则判断手机号格式的格式，正则判断失败抛出错误，否则直接callback()
        if (!/^1[2-9]\d{9}$/.test(value)) {
          callback(new Error("手机号格式不正确!"));
        } else {
          callback();
        }
      }

      let checkCode = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('请输入验证码！'));
        }

        if (!Number.isInteger(value)) {
          callback(new Error('请输入数字！'));
        } else if (!/^\d{6}$/g.test(value)) {
          callback(new Error('验证码位数不正确！'));
        } else {
          callback();
        }
      }

      return {
        formLogin: {
          phone: '',
          code: '',
          checked: false
        },
        loading: false,
        correctCode: '', //正确的验证码
        rules: {
          phone: [
            {required: true, message: '请输入手机号！', trigger: 'blur'},
            {validator: validatorPhone, trigger: 'change'}
          ],
          code: [
            {validator: checkCode, trigger: 'change'},
          ]
        }
      };

    },
    methods: {
      login() {
        this.$refs.formLogin.validate(valid => {
          if (!valid) {
            this.$message.error('请先规范填写表单!');
            return false;
          }

          this.loading = true;
          const {phone, code, checked} = this.formLogin;

          request({
            method: 'post',
            url: '/ouser/login',
            params: {phone: phone, code: code}
          }).then(res => {
            if (res.code === 1) {
              const token = res.data;
              storage.set('token', token, new Date().getTime() + 7 * 24 * 60 * 60 * 1000);
              if (checked) { //如果选择保存登录状态，则将此状态记录到storage中
                storage.set('rememberMe', true, new Date().getTime() + 7 * 24 * 60 * 60 * 1000);
              }
              this.$message.success('登录成功！');
              this.$router.push({name: 'home'});
            } else {
              this.$message.error(res.msg);
              this.formLogin = {phone: '', code: '', checked: false};
            }
          }).catch(error => {
            this.$message.error('服务器错误！');
            console.log(error);
          })

          this.loading = false;
          this.formLogin = {phone: '', code: '', checked: false};
        })

      },
      sendCode() {
        this.$refs.formLogin.validateField(['phone'], error => {
          if (error) {
            this.$message.error('获取验证码失败！');
            return;
          }

          request({
            method: 'get',
            url: '/ouser/getCode',
            params: {phone: this.formLogin.phone}
          }).then(res => {
            if (res.code === 1) {
              this.$message.success('验证码发送成功，请注意查收！',5);
            } else {
              this.$message.error(res.msg);
              this.formLogin = {phone: '', code: '', checked: false};
            }
          })
        })


      },
      rememberMe() {
        this.formLogin.checked = !this.formLogin.checked;
      }
    }
    ,
  }
</script>

<style scoped>
  .ant-form {
    margin-top: 5%;
  }

  /deep/ .ant-input-lg {
    height: 50px;
  }

  .ant-btn-lg {
    height: 48px;
  }

  .ant-input-search-button {
    height: 50px;
  }

  .form-group {
    margin-bottom: 1rem;
  }

  .checkbox {
    margin-top: -5%;
    margin-bottom: -6%;
  }
</style>